@import url(https://fonts.googleapis.com/css?family=Audiowide);
p {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
}
.adding_form {
  background: #111;
  border: 0.1rem solid #191919;
  border-radius: 0.5rem;
  bottom: 0;
  text-align: center;
  box-shadow: 0 0.5rem 1rem 0.5rem rgba(0, 0, 0, 0.2);
  margin: auto;
  overflow: hidden;
  width: 30rem;
}

.login_container .adding_form {
  height: 38rem;
  position: absolute;
  right: 0;
  top: 0;
  left: 0;
}

.adding_form::after {
  background: linear-gradient(
    to right,
    #111111,
    #444444,
    #b6b6b8,
    #444444,
    #2f2f2f,
    #272727
  );
  content: "";
  display: block;
  height: 0.1rem;
  left: 5rem;
  position: absolute;
  top: 0;
  width: 15rem;
}

.adding_form::before {
  border-radius: 50%;
  box-shadow: 0 0 0.6rem 0.4rem #fff;
  content: "";
  display: block;
  height: 0.5rem;
  left: 34%;
  position: absolute;
  top: -0.7rem;
  width: 0.8rem;
}

.inset {
  border-top: 0.1rem solid #19191a;
  padding: 2rem;
  text-align: left;
}

.light_heading {
  font-family: "Audiowide";
  border-bottom: 0.1rem solid #000;
  font-size: 2.5rem;
  padding: 1.5rem 0;
  position: relative;
  text-align: center;
  text-shadow: 0 0.1rem 0 #000;
}

label {
  font-size: 1.3rem;
}

.light_heading {
  color: var(--color-1);
  font-family: Audiowide;
  font-weight: normal;
}

.light_heading.poweron {
  color: #ffffff;
  transition: all 0.5s;
  -webkit-animation: flicker 1s ease-in-out 1 alternate,
    neon 1.5s ease-in-out infinite alternate;
  animation: flicker 1s ease-in-out 1 alternate,
    neon 1.5s ease-in-out infinite alternate;
  -webkit-animation-delay: 0s, 1s;
  animation-delay: 0s, 1s;
}
input:focus,
textarea:focus {
  border-bottom: none !important;
  box-shadow: 0 0.1rem 0 0 var(--color-1) !important;
}
.light_heading:after {
  position: absolute;
  width: 25rem;
  height: 18rem;
  content: "";
  display: block;
  pointer-events: none;
  top: 0;
  margin-left: 13.8rem;
  -webkit-transform-style: flat;
          transform-style: flat;
  -webkit-transform: skew(20deg);
          transform: skew(20deg);
  background: -ms-linear-gradient(
    top,
    hsla(0, 0%, 100%, 0.1) 0%,
    hsla(0, 0%, 100%, 0) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#42ffffff', endColorstr='#00ffffff',GradientType=0 );
  background: linear-gradient(
    to bottom,
    hsla(0, 0%, 100%, 0.1) 0%,
    hsla(0, 0%, 100%, 0) 100%
  );
}

input,
textarea {
  background: linear-gradient(#1f2124, #27292c);
  border: 0.1rem solid #222;
  border-radius: 0.3em !important;
  box-shadow: 0 0.1rem 0 rgba(255, 255, 255, 0.1);
  color: #fff;
  font-size: 1.3rem;
  margin-bottom: 2rem;
  padding: 1rem;
  box-sizing: border-box;
  width: 100%;
  border-bottom: none !important;
  outline: none !important;
}

input:disabled,
textarea:disabled {
  color: #999;
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  font-size: 1.4rem;
}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  font-size: 1.4rem;
}
