}

input:hover:not([disabled]),
input:focus,
input:hover:not([disabled]),
input:focus,
label:hover ~ input,
label:hover ~ input,
textarea:hover:not([disabled]),
textarea:focus,
textarea:hover:not([disabled]),
textarea:focus,
label:hover ~ textarea,
label:hover ~ textarea {
  background: #27292c;
}

@-webkit-keyframes neon {
  from {
    text-shadow: 0 0 0.25rem var(--neon), 0 0 0.5rem var(--neon),
      0 0 0.75rem var(--neon), 0 0 1rem var(--neon), 0 0 1.75rem var(--neon),
      0 0 2rem var(--neon), 0 0 2.5rem var(--neon), 0 0 3.75rem var(--neon);
  }

  to {
    text-shadow: 0 0 0.3rem var(--neon), 0 0 0.7rem var(--neon),
      0 0 1.3rem var(--neon), 0 0 1.7rem var(--neon), 0 0 3.3rem var(--neon),
      0 0 3.8rem var(--neon), 0 0 4.8rem var(--neon), 0 0 6.3rem var(--neon);
    color: var(--neon);
  }
}

