.c-btn__continue {
  background: var(--color-1);
  width: 20rem;
  height: 4rem;
  color: white;
  border: 0;
  border-radius: 1.5em;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  font-weight: 600;
  position: relative;
  overflow: hidden;
}
.c-btn__continue .u-loading,
.c-btn__continue .u-success {
  display: none;
}
.c-btn__continue:hover {
  opacity: 0.9;
}
.c-btn__continue:active {
  -webkit-transform: scale(0.97);
          transform: scale(0.97);
}

.c-btn__continue:focus {
  background-color: var(--color-1);
}

.c-btn__loading span {
  display: none;
}
.c-btn__loading .u-loading {
  display: block;
  margin: 0 auto;
  width: 90%;
  stroke-dasharray: 200;
  stroke-dashoffset: 200;
  -webkit-animation: dash 1s ease-in infinite alternate;
  animation: dash 1s ease-in infinite alternate;
}

.c-btn__finished span,
.c-btn__finished .u-loading {
  display: none;
}
.c-btn__finished .u-success {
  display: block;
  margin: 0 auto;
  -webkit-animation: fade-in 500ms ease-in;
  animation: fade-in 500ms ease-in;
}

.u-loading {
  width: 2.5rem;
  height: 2.5rem;
}

.u-success {
  width: 3rem;
  height: 3rem;
}

.u-hide {
  display: none;
}

.u-logo {
  position: absolute;
  right: 1em;
  bottom: 1em;
  color: white;
}
.u-logo__svg {
  margin-left: 0.5em;
}
.u-logo:hover {
  opacity: 0.9;
}

@-webkit-keyframes dash {
  0% {
    stroke-dashoffset: 200;
  }
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes dash {
  0% {
    stroke-dashoffset: 200;
  }
  100% {
    stroke-dashoffset: 0;
  }
}
@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
    -webkit-transform: translateY(40%);
            transform: translateY(40%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
    -webkit-transform: translateY(40%);
            transform: translateY(40%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

